s-theme/src/common/colors.scss';\n\n.icons-view {\n  padding: 12px 32px 32px;\n\n  .icons-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 12px;\n    h1 { display: inline-block; }\n    .el-input { flex: 1 }\n\n    .icons-header__inputs {\n      display: flex;\n      gap: 24px;\n      align-items: center;\n      width: calc(40% + 52px)\n    }\n  }\n\n  .icons-list {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 24px;\n\n    .icons-list__list-item {\n      padding: 24px 12px;\n      border: 1px solid rgb(220, 223, 230);\n      border-radius: 4px;\n      width: calc(20% - 20px);\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      justify-content: center;\n      transition: .2s ease-in-out;\n      cursor: pointer;\n\n      &--dark {\n        background-color: #979797;\n        .icons-list__name { color: #fff !important; }\n      }\n\n      &:hover {\n        border: 1px solid $--color-primary;\n      }\n\n      .icons-list__icon-svg {\n        width: 40px;\n      }\n\n      .icons-list__icon-font {\n        font-size: 32px;\n        line-height: 1;\n      }\n\n      .icons-list__name {\n        margin-top: 24px;\n        color: #979797;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
